<script>
  import Vue from "vue"
  import $ from "jquery";
  import {
    Dialog
  } from 'vant';

  export default {
    name: "MyOrderForm",
    components: {},
    data() {
      return {
        obtainshow: false,
        Astateful:true,
      }
    },
    methods: {
      prev(){
        this.$router.go(-1)
      },
      //确认收货
      obtain() {
        this.obtainshow = true
      },
      //查看问题
      ExamineIssue() {
        this.$router.push({
          path: "/InstantMessaging"
        })
      },
    },
    created() {
    },
    //关于顶部的分类方法
    mounted() {
      // 监听并添加蒙版
      $(document).ready(function() {
        $($(".boxdiv li")[0]).addClass("choose");
        $('.boxdiv').on('scroll', function(e) {
          if ($(this).scrollLeft() === 0) {
            console.log($(this));
            $(this).find("ul").addClass("extrudeLeft");
            $(this).find("ul").removeClass("extrudeRight");
          }
          console.log($(this).width(), $(this).scrollLeft(), $('.boxdiv ul').width());
          if (($(this).width() + $(this).scrollLeft()) === Math.round($('.boxdiv ul').width())) {
            $(this).find("ul").addClass("extrudeRight");
            $(this).find("ul").removeClass("extrudeLeft");
          } else {
            $(this).removeClass("extrudeLeft");
            $(this).removeClass("extrudeRight")
          }
        });
        //滚动动画
        $(".boxdiv li").click(function() {
          $(this).addClass("choose");
          $(this).siblings().removeClass("choose");
          console.log($(this).siblings());
          //滚动动画
          if ($(this).index() == 3 || $(this).index() == 4) {
            // $(this).scrollLeft()="192"
            console.log($(this).scrollLeft());
            $(".boxdiv").animate({
              scrollLeft: 208 //让body的scrollTop等于pos的top，就实现了滚动
            }, 1000);
          } else if ($(this).index() == 2 || $(this).index() == 1) {
            $(".boxdiv").animate({
              scrollLeft: 0 //让body的scrollTop等于pos的top，就实现了滚动
            }, 1000);
          }
          console.log($(this).scrollLeft())
        })
      })
    },
  }
</script>
