    padding-left: 0.3rem;
    margin-right: 0.3rem;
    padding-right: 0.3rem;
    /*background: whitesmoke;*/
    height: 0.91rem;
    line-height: 0.91rem;
    white-space: nowrap;
    display: inline-block;
    margin-right: -4px;
    position: relative;
  }

  .boxdiv ul li:last-child {
    margin-right: 0;
  }

  .boxdiv ul::-webkit-scrollbar {
    display: none;
  }


  .CountSwiper {
    width: 100%;
    height: 0.91rem;
    position: relative;
    background: #fff;
    border-bottom: 1px solid #e3e4e8;
    overflow: hidden;
  }

  .extrudeLeft:after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    background: url(../../../assets/img/common/CountSwiperleft.png) no-repeat center;
    width: 0.64rem;
    height: 0.91rem;
    z-index: 1;
    opacity: 0.5;
  }

  .extrudeRight:before {
    content: "";
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
    background: url(../../../assets/img/common/CountSwiperright.png) no-repeat center;
    width: 0.64rem;
    height: 0.91rem;
    z-index: 2;
    opacity: 0.5;
  }

  .choose:after {
    content: "";
    display: table;
    clear: both;
    position: absolute;
    bottom: 0;
    width: 0.36rem;
    left: 50%;
    margin-left: -0.18rem;
    background: red;
    height: 0.08rem;
  }

  .CountSwiper .choose {
    font-size: 0.34rem;
    font-weight: bold;
    color: #333;
  }

  .van-dialog {
    padding-top: .48rem;
    border-radius: inherit;
    font-size: .38rem;
  }
</style>



